
.navbar {
  overflow: hidden;
  background-color: #333;
  position: fixed;
 
  width: 100%;

  left: 0;
  bottom: 0;
  color: white;
  text-align: center;
}

.navbar a {

  display: block;
  color: #f2f2f2;

  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

.navbar a:hover {
  background: #ddd;
  color: black;
}
